<template>
  <v-container>
    <section class="pb-12">
      <h2 class="text-center mb-2">
        {{ $t("Add_offer") }}
      </h2>
      <label class="font-weight-bold">
        {{ $t("Area") }}
      </label>
      <Multiselect
        :options="optionsarea"
        :searchable="true"
        :placeholder="$t('Area')"
        label="name"
        class="selectnew"
        v-model="formData.area_id"
        @change="areachange"

      />
      <label class="font-weight-bold">
        {{ $t("City") }}
      </label>
      <multiselect
        :options="optionscity"
        :searchable="true"
        :selected="null"
        :placeholder="$t('City')"
        label="name"
        class="selectnew"
        v-model="formData.city_id"
        @change="citychange"
      ></multiselect>
      <label class="font-weight-bold">
        {{ $t("neighborhood") }}
      </label>
      <multiselect
        :options="optionsneighborhood"
        :searchable="true"
        :placeholder="$t('neighborhood')"
        label="name"
        class="selectnew"
        v-model="formData.neighborhood_id"
        @change="neighborhoodchange"
      ></multiselect>
      <label class="font-weight-bold">
        {{ $t("estate_type") }}
      </label>
      <Multiselect
        :options="optionsestate"
        :searchable="true"
        :placeholder="$t('estate_type')"
        label="name"
        class="selectnew"
        v-model="formData.estate_type_id"
        @change="estate($event)"
      />
      <label class="font-weight-bold">
        {{ $t("ads_type") }}
      </label>
      <Multiselect
        :options="optionsadtype"
        :searchable="true"
        :placeholder="$t('ads_type')"
        label="name"
        class="selectnew"
        v-model="formData.ad_type_id"
      />
      <label class="font-weight-bold">
        {{ $t("usage_type") }}
      </label>
      <Multiselect
        :options="optionsusagetype"
        :searchable="true"
        :placeholder="$t('usage_type')"
        label="name"
        class="selectnew"
        v-model="formData.usage_type_id"
        @change="usage($event)"
      />
      <label class="font-weight-bold">
        {{ $t("description") }}
      </label>
      <v-text-field
        variant="solo"
        class="selectnew"
        v-model="formData.description"
      ></v-text-field>
      <!-- <label class="font-weight-bold">
        {{ $t("address") }}
      </label>
      <v-text-field
        variant="solo"
        class="selectnew"
        v-model="formData.address"
      ></v-text-field> -->
      <label class="font-weight-bold">
        {{ $t("Location_Details") }}
      </label>
      <v-text-field
        variant="solo"
        class="selectnew"
        v-model="formData.location"
      ></v-text-field>
      <label class="font-weight-bold">
        {{ $t("lat") }}
      </label>
      <v-text-field
      type="number"
        variant="solo"
        class="selectnew"
        v-model="formData.lat"
      ></v-text-field>
      <label class="font-weight-bold">
        {{ $t("lang") }}
      </label>
      <v-text-field
      type="number"
        variant="solo"
        class="selectnew"
        v-model="formData.lng"
      ></v-text-field>
      <label class="font-weight-bold">
        {{ $t("price") }}
      </label>
      <v-text-field
        type="number"
        variant="solo"
        class="selectnew"
        v-model="formData.price"
      ></v-text-field>
    

      <label class="font-weight-bold">
        {{ $t("Aarea") }}
      </label>
      <v-text-field
        type="number"
        variant="solo"
        class="selectnew"
        v-model="formData.area"
      ></v-text-field>
   
      <!-- <label class="font-weight-bold">
        {{ $t("mortgage") }}
      </label>
      <v-text-field
        type="number"
        variant="solo"
        class="selectnew"
        v-model="formData.mortgage"
      ></v-text-field> -->
    
      <label class="font-weight-bold">
        {{ $t("advertiser_orientation") }}
      </label>
      <Multiselect
        :options="optionsadvertiserorientation"
        :searchable="true"
        :placeholder="$t('advertiser_orientation')"
        label="name"
        class="selectnew"
        v-model="formData.advertiser_orientation_id"
      />
      <label class="font-weight-bold">
        {{ $t("advertiser_type") }}
      </label>
      <Multiselect
        :options="optionadvertisertype"
        :searchable="true"
        :placeholder="$t('advertiser_type')"
        label="name"
        class="selectnew"
        v-model="formData.advertiser_type"
      />
      <label class="font-weight-bold">
        {{ $t("license_number") }}
      </label>
      <v-text-field
        type="number"
        variant="solo"
        class="selectnew"
        v-model="formData.license_number"
      ></v-text-field>
      <label class="font-weight-bold">
        {{ $t("delegation_number") }}
      </label>
      <v-text-field
        type="number"
        variant="solo"
        class="selectnew"
        v-model="formData.delegation_number"
      ></v-text-field>
      <!-- <label class="font-weight-bold">
        {{ $t("advertiser_registration_number") }}
      </label>
      <v-text-field
        type="number"
        variant="solo"
        class="selectnew"
        v-model="formData.advertiser_registration_number"
      ></v-text-field> -->
      <v-checkbox
        :label="$t('special')"
        class="check"
        @change="checkspcial"
      ></v-checkbox>
      <v-checkbox label="Story" class="check" @change="checked" v-if="story"></v-checkbox>
      <label class="font-weight-bold">
        {{ $t("image") }}
      </label>
      <v-file-input
        variant="solo"
        class="selectnew my-2"
        v-model="formData.attachment"
        label="Add Image"
        prepend-icon="mdi-camera"
        accept=".pdf, image/jpeg, image/png"
        @change="handleFileChange"
      ></v-file-input>
      <v-row class="mt-2">
        <v-col cols="12" md="3" v-for="item in optionproperties" :key="item.id">
          <label class="font-weight-bold">
            {{ item.name }}
          </label>
          <input
            type="range"
            class="d-block form-control selectnew"
            :min="item.min_value"
            :max="item.max_value"
            style="border-style: solid"
            v-if="item.type == 'slider'"
            v-model="optionsAdd[item.id]"
          />
          <Multiselect
            v-else-if="item.type == 'multi'"
            :options="item.values_ar"
            :searchable="true"
            class="selectnew"
            v-model="optionsAdd[item.id]"
          />
          <v-checkbox
            v-else-if="item.type == 'switch'"
            class="check"
            v-model="optionsAdd[item.id]"
          ></v-checkbox>

          <v-text-field
            v-else
            :type="item.type"
            variant="solo"
            class="selectnew"
            :min="item.min_value"
            :max="item.max_value"
            v-model="optionsAdd[item.id]"
          ></v-text-field>

          <!-- @change="prepareAndAddData(item.id, $event, item.name)" -->
        </v-col>
      </v-row>
      <div class="d-flex align-center">
        <v-checkbox class="check d-flex flex-0-0" @change="check"></v-checkbox>
        <label @click="dialog = true">
          <h3 style="cursor: pointer">
            {{ $t("Conditions_advertisement") }}
          </h3>
        </label>
      </div>
      <div class="text-red">{{ errorMessage ? errorMessage : "" }}</div>

      <div class="text-center">
        <button class="previewads w-50 pa-2 mt-5" @click="addnewad">
          {{ $t("Add_new_ad") }}
        </button>
      </div>
      <v-dialog v-model="dialog" width="700">
        <v-card class="text-center py-5">
          <h3
            class="w-max-content mx-auto rounded pa-1 text-h5 font-weight-bold bg-yellow-darken-1"
          >
            {{ $t("Conditions_advertisement") }}
          </h3>
          <p class="pa-5 text-h6">
                          
                         
                      .
                         
                        
                       
                    .   
                      
                         
                     .  
                         
                  .     .
          </p>
        </v-card>
      </v-dialog>
    </section>
    <v-snackbar
      v-model="showSnackbar"
      color="yellow-darken-2
"
    >
      {{ $t("Added_successfully") }}
      <template v-slot:action="{ attrs }">
        <v-btn color="pink" v-bind="attrs" @click="showSnackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
import Multiselect from "@vueform/multiselect";
import crudDataService from "@/Services/crudDataService";
export default {
  components: {
    Multiselect,
  },
  data() {
    return {
      showSnackbar: false,
      errorMessage: "",
      eventchange: "",
      dialog: false,
      optionscity: [],
      optionsarea: [],
      optionsneighborhood: [],
      optionsestate: [],
      optionsadtype: [],
      optionsusagetype: [],
      optionsmaintype: [],
      optionsadvertiserorientation: [],
      optionadvertisertype: [],
      nei_id: "",
      formData: {
        city_id: "",
        neighborhood_id: "",
        area_id: "",
        estate_type_id: "",
        ad_type_id: "",
        usage_type_id: "",
        main_type: "sell",
        description: "",
        address: "",
        location: "",
        lng: "",
        lat: "",
        price: "",
        area: "",
        estate_notes: 0,
        advertiser_orientation_id: "",
        advertiser_type: "",
        license_number: "",
        delegation_number: "",
        advertiser_registration_number: "",
        attachment: [],
        is_story: 0,
        special: 0,
        options: [],
        usage_id: "",
        estate_id: "",
      },
      optionsAdd:[],
      optionproperties: [],
      story:true,
      areaname:'',
      cityname:'',
      neighborhoodname:""

    };
  },
  methods: {
    async getstories() {
      let response = await crudDataService.getAll("stories");
      if (response.data.data.ads[0]) {
        this.story = response.data.data.ads[0].is_story==1?false:true;
      console.log(this.story);
      }
    },
    handleFileChange(event) {
      this.formData.attachment[0] = event.target.files.item(0);
    },
    checked(e) {
      if (e.target.checked) {
        this.formData.is_story = 1;
      } else {
        this.formData.is_story = 0;
      }
    },
    check(e) {
      if (e.target.checked) {
        this.formData.estate_notes = 1;
      } else {
        this.formData.estate_notes = 0;
      }
    },
    checkspcial(e) {
      if (e.target.checked) {
        this.formData.special = 1;
      } else {
        this.formData.special = 0;
      }
    },
  
    async Areas() {
      let response = await crudDataService.getAll(`areas`);
      this.optionsarea = response.data.data.map((state) => ({
        value: state.id,
        name: state.name,
      }));
    },
   
    areachange(e,name){
      this.areaid=e;
      this.areaname=name.ariaLabel
      this.City();
    },
    
    async City() {
      let response = await crudDataService.getAll(`cities?area_id=${this.areaid}`);
      this.optionscity = response.data.data.map((state) => ({
        value: state.id,
        name: state.name,
      }));
    },
    citychange(e,name) {
      this.nei_id = e;
      this.cityname=name.ariaLabel
      this.neighborhood_id();
    },
    neighborhoodchange(e,name) {
      this.neighborhoodname=name.ariaLabel
    },
    async neighborhood_id() {
      let response = await crudDataService.getAll(
        `neighborhoods?city_id=${this.nei_id}`
      );
      this.optionsneighborhood = response.data.data.map((state) => ({
        value: state.id,
        name: state.name,
      }));
    
    
    },
    async estate_types() {
      let response = await crudDataService.getAll(`estate-types`);
      this.optionsestate = response.data.data.map((state) => ({
        value: state.id,
        name: state.name,
      }));
    },
    async ad_types() {
      let response = await crudDataService.getAll(`ad-types`);
      this.optionsadtype = response.data.data.map((state) => ({
        value: state.id,
        name: state.name,
      }));
    },
    async usage_types() {
      let response = await crudDataService.getAll(`usage-types`);
      this.optionsusagetype = response.data.data.map((state) => ({
        value: state.id,
        name: state.name,
      }));
    },

    async advertiser_orientation() {
      let response = await crudDataService.getAll(`advertiserOrientation`);
      this.optionsadvertiserorientation = response.data.data.map((state) => ({
        value: state.id,
        name: state.name,
      }));
    },
    async advertiser_type() {
      this.optionadvertisertype = [
        { value: "owner", name: this.$t("owner") },
        { value: "delegate", name: this.$t("delegate") },
      ];
    },
  
    estate(e) {
      this.estate_id = e;
      console.log(e);
    },
    usage(e) {
      this.usage_id = e;
      console.log(e);
      this.properties();
    },
    async properties() {
      let response = await crudDataService.getAll(
        `properties/${this.estate_id}/${this.usage_id}`
      );
      this.optionproperties = response.data.data;
    },
    prepareAndAddData() {
  this.formData.options = Object.entries(this.optionsAdd).map(([key, value]) => {
    return {
      property_id: key,
      values: [value], // Assuming each value is what you want to add
    };
  });
},
      
    async addnewad() {
      this.prepareAndAddData()
      this.formData.address=  this.areaname+' '+this.cityname+' '+this.neighborhoodname

console.log(this.formData.options);
      let response = await crudDataService
        .create(`store-ad`, this.formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then(() => {
          this.showSnackbar = true;
          setTimeout(() => {
            this.$router.push("/");
          }, 5000);
        })
        .catch((error) => {
          if (error.response) {
            // The API responded with an error message
            this.errorMessage = error.response.data.message;
          } else if (error.request) {
            this.errorMessage = "No response from the server";
          } else {
            this.errorMessage = error.message;
          }
        });
    },
  },
  mounted() {
    this.Areas();
    this.City();
    this.estate_types();
    this.ad_types();
    this.usage_types();
    this.advertiser_orientation();
    this.advertiser_type();
    this.getstories()
  },
};
</script>

<style lang="scss">
.selectnew {
  &.multiselect {
    margin-top: 8px;
    margin-bottom: 10px;
    border-radius: 10px;
  }
  .multiselect-search {
    border-radius: 10px;
  }
}
.selectnew.v-input--density-default .v-field--variant-outlined,
.selectnew.v-input--density-default .v-field--single-line,
.selectnew.v-input--density-default .v-field--no-label {
  box-shadow: none !important;
  border: 1px solid #d1d5db !important;
  border-radius: 10px !important;
  margin-top: 6px;
  margin-bottom: 8px;
}
.check .v-input__details,
.selectnew .v-input__details {
  display: none !important;
}
.selectnew.v-text-field input.v-field__input {
  min-height: 20px;
  padding-top: 7px;
  padding-bottom: 7px;
}
</style>
